import { Block } from '@ali/4ever-cangjie';
import { INDENT_SIZE } from '@ali/4ever-utils';

/**
 * @description 根据普通段落的缩进，获取列表中匹配的 level 值
 * */
export default function getMatchLevel(block: Block): number {
  let level = 0;
  if (block?.data?.ind) {
    const firstLine = block.data.ind.firstLine || 0;
    const left = block.data.ind.left || 0;
    level = Math.floor((firstLine + left) / INDENT_SIZE);
  }
  return level;
}
